import React from 'react';
import TextField from 'material-ui/TextField'
import SelectField from 'material-ui/SelectField';
import MenuItem from 'material-ui/MenuItem';




class FarmasiPage extends React.Component {

    state = {
        value: 1,
    };


    handleChange = (event, index, value) => this.setState({value});

    render() {
        return(
          <div>
              <span>
                  <TextField
                      floatingLabelText="Pencarian nama obat"
                      floatingLabelFixed={true}
                  />
                  <SelectField
                      floatingLabelText="Kategori"
                      value={this.state.value}
                      onChange={this.handleChange}
                      style={{width: 150}}
                  >
                      <MenuItem value={1} primaryText="Never" />
                      <MenuItem value={2} primaryText="Every Night" />
                      <MenuItem value={3} primaryText="Weeknights" />
                      <MenuItem value={4} primaryText="Weekends" />
                      <MenuItem value={5} primaryText="Weekly" />
                  </SelectField>
                  <SelectField
                      floatingLabelText="Jenis"
                      value={this.state.value}

                  >
                      <MenuItem value={1} primaryText={"Minum"}/>
                      <MenuItem value={2} primaryText={"Telen"}/>
                  </SelectField>
              </span>
          </div>
        );
    }
}

export default FarmasiPage;


